<template>
  <div class="infinite-list-wrapper1"  v-loading="creating">
    <MethodList :title="title" :code="code" :note="note" :count="count" :maxCnt="maxCnt" @append="append"></MethodList>
  </div>
</template>

<script>
import MethodList from "../components/MethodList.vue"

export default {
    name: "Skill",
    components : {
        MethodList
    },

    data () {
      return {
        creating: true,
        count: 0,
        maxCnt: 1,

        title:[],
        code:[],
        note:'',
      }
    },

    methods: {
      append:function(){
              fetch(this.$store.state.fetchUrl + '/appendSkill', {
                method:'POST',
                mode:'cors',
                body: JSON.stringify({
                  'index': this.count + 1,
                  'nums': 3
               })
              }).then(res => res.json())
              .then(data => {
                this.title.push.apply(this.title, data['name'])
                this.code.push.apply(this.code, data['code'])
                this.count += data['nums']
              })
      }
    },

    mounted(){
        window.addEventListener('scroll', this.handleScroll, true);  // 监听（绑定）滚轮滚动事件
    },

    destroyed() {
        window.removeEventListener('scroll', this.handleScroll);   //  离开页面清除（移除）滚轮滚动事件
    },

    created(){
        fetch(this.$store.state.fetchUrl + '/loadSkill', {
          method:'POST',
          mode:'cors',
          body: JSON.stringify({
            'index': this.count + 1,
            'nums': 3
          })
        }).then(res => res.json())
        .then(data => {
          this.maxCnt = data['maxCnt']
          this.title.push.apply(this.title, data['name'])
          this.code.push.apply(this.code, data['code'])
          this.count += data['nums']
          setTimeout(() => {this.creating = false}, 200)
        })
    }
  }
</script>

<style>
.infinite-list-wrapper1 {
    color: #333;
    text-align: center;
    line-height: 160px;
}
</style>