<template>
    <el-popover
        placement="left"
        width="400"
        trigger="hover">
        <ul style="list-style-type:none;overflow:auto;height:300px;">
        <li v-for="index in 107" :key="index" style="float:left;" @click="emitFace(index)">
            <el-card 
                shadow="hover" 
                class="face-card" 
                style="border-width:2px;width:40px;height:40px;margin:5px;" 
                :body-style="{ padding: '0px' }">
                <img :src="getImgSrc(index)" style="width:38px;height:38px;">
            </el-card>
        </li>
        </ul>
        <el-button style="margin:0 10px;" slot="reference" type="primary" plain >^_^</el-button>
    </el-popover>
</template>

<script>
export default {
    props:['textarea'],

    data(){
        return{
            
        }
    },

    computed:{
        getImgSrc(){
            return function(i){
              return require("../assets/face/图片" + (i - 1).toString() + ".jpg")
            }
        }
    },

    methods:{
        emitFace:function(i){
            this.$emit('FromFaceBox', '@pokemon('+ (i - 1).toString() +')');
        }
    },
}
</script>

<style>
.face-card:hover{
  border-color:#b3d8ff;
  border-width: 2px;
}
</style>