<template>
    <div>
        <ul style="list-style-type:none;">
        <li v-for="(item, index) in realContent" :key="index">
                <el-card class="card3" :style="cardStyleList[colID[index]%7]">
                    <div>
                    <strong>{{subject[index]}}----{{label[index]}}</strong>
                    <el-button-group style="float:right;">
                    <el-button v-show="isRiver" style="margin-right:15px;" class="delete-button" type="text" icon="el-icon-edit" @click="modifyNote(index)"></el-button>
                    <el-button v-show="isRiver" class="delete-button" type="text" icon="el-icon-delete" @click="deleteNote(index)"></el-button>
                    </el-button-group>
                    </div>
                    <el-divider></el-divider>
                    <div style="white-space:pre-wrap;" v-html="item"></div>
                </el-card>
        </li>
        </ul>

        <el-drawer
        :visible.sync="drawer"
        direction="btt"
        size='450px'>
            <el-input
                style="margin-left:8%; width:80%;"
                type="textarea"
                :autosize="{ minRows: 14, maxRows: 14}"
                v-model="textarea">
            </el-input><br>
            <div style="float:right; margin-right: 12%; margin-top:20px;">
            <FaceBox :textarea="textarea" @FromFaceBox="insertFace"></FaceBox>
            <ColorBox :textarea="textarea" @FromColorBox="colorWord"></ColorBox>
            <el-button type="primary" plain @click="submitChange">OK !</el-button>
            </div>
        </el-drawer>
    </div>
</template>

<script>
import FaceBox from "../components/FaceBox.vue"
import ColorBox from "../components/ColorBox.vue"

export default {
    name: "NoteList",
    components : {
      FaceBox,
      ColorBox
    },
    props: ['content', 'realContent', 'colID', 'subject', 'label', 'time'],

    data(){
        return{
            drawer:false,
            textarea:'',
            cardStyleList: [
                "border-color: #4CB8C4;",
                "border-color: #3C3B3F;",
                "border-color: #DD5E89;",
                "border-color: #EB3349;",
                "border-color: #11998e;",
                "border-color: #1488CC;",
                "border-color: #16A085;"
            ],
            colorName:['red', 'blue', 'green', 'pink']
        }
    },

    computed:{
        isRiver(){
            return  sessionStorage.getItem("userName") == 'River' || this.$store.getters.currentUser == 'River'
        },
    },

    methods:{
      colorWord:function(res){
          this.textarea = res
      },

      modifyNote:function(i){
          this.cur = i
          this.textarea = this.content[i]
          this.drawer = true
      },

      insertFace(res){
          this.textarea += res
      },

      submitChange:function(){
          var reg = new RegExp("\'","g");
          this.textarea = this.textarea.replace(reg, "\'\'");
          fetch(this.$store.state.fetchUrl + "/changeNote", {
              method:'POST',
              mode:'cors',
              body: JSON.stringify({
                  'content':this.textarea,
                  'time':this.time[this.cur]
              })
          }).then(res => res.text())
          .then(data => {
              if(data == 'success'){
                  this.$message({
                  type: 'success',
                  message: '修改成功!'
              })
              this.drawer = false
              this.$emit('reload')
              }
              else this.$message.error('出错了...');
          }).catch(err =>{
              console.log(err)
          })
      },

      deleteNote: function(i){
        this.$confirm('此操作将永久删除该笔记, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {

        fetch(this.$store.state.fetchUrl + "/deleteNote", {
          method: 'POST',
          mode: 'cors',
          body: JSON.stringify({
            'time': this.time[i]
          })
        }).then(res => res.text())
        .then(data => {
            if(data == 'success'){
              this.$message({
                type: 'success',
                message: '删除成功!'
              })
              this.totalNum -= 1
              this.$emit('reload')
            }
            else this.$message.error('出错了...');
        })

        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      },


    }
}
</script>

<style>
.card3{ 
    border-radius: .8125rem;
    font-size: 0.9625rem;
    font-weight: 500;
    color: rgb(94, 92, 92);
    margin: 20px;
}


.el-card__header {
    padding: 10px 25px;
}
.delete-button{
  float: right; 
  padding: 3px 0;
}

.delete-button:hover{
  color: rgb(0, 0, 0);
}
</style>