<template>
<div>
    <el-button-group v-show="isRiver" style="margin-bottom:30px;">
        <el-button type="primary" icon="el-icon-edit" round @click="modifyCode">修改</el-button>
        <el-button type="primary" icon="el-icon-delete" round @click="deleteCode">删除</el-button>
    </el-button-group>

    <el-drawer
    :visible.sync="drawer"
    direction="btt"
    size='450px'>
        <el-input
            style="margin-left:8%;margin-bottom:8px; width:80%; height:30%;"
            type="textarea"
            :autosize="{ minRows: 5, maxRows: 5}"
            v-model="notearea">
        </el-input>

        <el-input
            style="margin-left:8%; width:80%; height:60%;"
            type="textarea"
            :autosize="{ minRows: 12, maxRows: 12}"
            v-model="textarea">
        </el-input>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<el-button type="primary" plain @click="submitChange">OK !</el-button>
    </el-drawer>

    <div>
    <el-card shadow="hover" class="detail-card">
    <span><strong>{{this.title}}</strong></span>
    <el-divider></el-divider>
    <pre  style="margin:0;font-family:'微软雅黑';font-size: .9625rem;font-weight: 550; color: #5e5c5c;"><span>{{this.note}}</span></pre>
    <pre  style="margin:0;" v-highlightjs>
        <code class="cpp" v-text="code">
        </code>
    </pre>
    </el-card>
    </div>
</div>
</template>

<script> 
    export default {
        data(){
            return{
                title: '',
                code:'',
                drawer:false,
                textarea:'',
                note:'',
                notearea:''
            }
        },

        computed:{
            isRiver(){
                return  sessionStorage.getItem("userName") == 'River' || this.$store.getters.currentUser == 'River'
            }
        },

        methods: {
            modifyCode:function(){
                this.textarea = this.code
                this.notearea = this.note
                this.drawer = true
            },

            submitChange:function(){
                var reg = new RegExp("\'","g");
                this.textarea = this.textarea.replace(reg, "\'\'");
                fetch(this.$store.state.fetchUrl + "/changeCode", {
                    method:'POST',
                    mode:'cors',
                    body: JSON.stringify({
                        'title':this.title,
                        'code':this.textarea,
                        'note':this.notearea
                    })
                }).then(res => res.text())
                .then(data => {
                    if(data == 'success'){
                       this.$message({
                        type: 'success',
                        message: '修改成功!'
                    })
                    this.code = this.textarea
                    this.note = this.notearea
                    this.drawer = false
                    }
                    else this.$message.error('出错了...');
                }).catch(err =>{
                    console.log(err)
                })
            },

            deleteCode:function(){
                this.$confirm('此操作将永久删除该代码, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {

                fetch(this.$store.state.fetchUrl + "/deleteCode", {
                    method: 'POST',
                    mode: 'cors',
                    body: JSON.stringify({
                        'title': this.title
                    })
                }).then(res => res.text())
                .then(data => {
                    if(data == 'success'){
                        this.$message({
                        type: 'success',
                        message: '删除成功!'
                    })
                    this.$router.push('/login')
                    }
                    else this.$message.error('出错了...');
                })

                }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });          
        });
            }
        },

        created(){
            this.code = this.$route.query.code
            this.title = this.$route.query.title
            this.note = this.$route.query.note
        },

    }
</script>

<style>
.el-drawer__header{
    margin-bottom: 0px;
}

.cpp{
    font-size: 15px!important;
    line-height: 1.5!important;
    font-family: monospace;
}

.el-divider--horizontal {
    display: block;
    height: 1px;
    width: 100%;
    margin: 10px 0;
}

.detail-card{
    background-color: rgb(227, 241, 252);
}
</style>