<template>
  <div>
    <el-input
    type="textarea"
    :autosize="{ minRows: 10}"
    placeholder="随便写点什么吧 ~~"
    v-model="textarea">
    </el-input><br><br>
    <FaceBox :textarea="textarea" @FromFaceBox="insertFace"></FaceBox>
    <ColorBox :textarea="textarea" @FromColorBox="colorWord"></ColorBox>
    <el-button type="primary" plain @click="submitDiary">OK !</el-button>
  </div>
</template>

<script>
import FaceBox from "../components/FaceBox.vue"
import ColorBox from "../components/ColorBox.vue"

export default {
  name: "AddDiary",
  components : {
    FaceBox,
    ColorBox
  },

   data() {
     return {
       textarea: '',
     }
   },

   methods:{
      colorWord:function(res){
          this.textarea = res
      },
      
     insertFace(res){
        this.textarea += res
     },

     submitDiary:function(){
       if(this.textarea == ''){
          this.$message({
            message: '不能为空！！',
            type: 'warning'
          })
          return
       }
       var reg = new RegExp("\'","g");
       this.textarea = this.textarea.replace(reg, "\'\'");

       fetch(this.$store.state.fetchUrl + '/submitDiary', {
         method: 'POST',
         mode:'cors',
         body:JSON.stringify({
           'content': this.textarea
         })
       }).then(res => res.text())
       .then(data => {
         if(data == 'success'){
            this.$message({
              message: '提交成功 ~~',
              type: 'success'
          })
          this.textarea = ''
         }
         else{
            this.$message.error('出错了...')
         }
       })
     },
   }
}
</script>