<template>
  <div>
    <el-input  placeholder="科目" v-model="subject" style="margin-bottom:20px;margin-left:10%;width:760px;"></el-input><br>
    <el-input  placeholder="标签" v-model="label" style="margin-bottom:20px;margin-left:10%;width:760px;"></el-input><br>
    <el-input
    type="textarea"
    :autosize="{ minRows: 10}"
    placeholder="记笔记 ！！"
    style="width:760px;margin-left:10%;"
    v-model="textarea">
    </el-input><br><br>
      <FaceBox :textarea="textarea" @FromFaceBox="insertFace"></FaceBox>
      <ColorBox :textarea="textarea" @FromColorBox="colorWord"></ColorBox>
    <el-button type="primary" plain @click="submitNote">OK !</el-button>
  </div>
</template>

<script>
import FaceBox from "../components/FaceBox.vue"
import ColorBox from "../components/ColorBox.vue"

export default {
  name: "AddNote",
  components : {
    FaceBox,
    ColorBox
  },

   data() {
     return {
       textarea: '',
       label:'',
       subject:''
     }
   },

   methods:{
      colorWord:function(res){
          this.textarea = res
      },

     submitNote:function(){
       if(this.textarea == '' || this.label == ''){
          this.$message({
            message: '不能为空！！',
            type: 'warning'
          })
          return
       }
       var reg = new RegExp("\'","g");
       this.textarea = this.textarea.replace(reg, "\'\'");

       fetch(this.$store.state.fetchUrl + '/submitNote', {
         method: 'POST',
         mode:'cors',
         body:JSON.stringify({
           'content': this.textarea,
           'label':this.label,
           'subject':this.subject
         })
       }).then(res => res.text())
       .then(data => {
         if(data == 'success'){
            this.$message({
              message: '提交成功 ~~',
              type: 'success'
          })
          this.textarea = ''
         }
         else{
            this.$message.error('出错了...')
         }
       })
     },

     insertFace(res){
        this.textarea += res
     },
     
   }
}
</script>