<template>
  <div>
    <el-select v-model="value" placeholder="请选择">
        <el-option
            v-for="item in options"
            :key="item.value"
            :value="item.value">
        </el-option>
    </el-select>
    <el-input
    type="textarea"
    style="width:70%;"
    :autosize="{ minRows: 1, maxRows: 2}"
    placeholder="技巧简介"
    v-model="textarea1">
    </el-input>
    <div style="margin: 20px 0;"></div>
    <el-input
    type="textarea"
    :autosize="{ minRows: 20}"
    placeholder="代码示例"
    v-model="textarea2">
    </el-input><br><br>
    <el-button type="primary" plain @click="submitSkill">OK !</el-button>
  </div>
</template>

<script>
export default {
   data() {
     return {
       textarea1: '',
       textarea2: '',

       options: [{
          value:  '搜索'
        }, {
          value:'排序'
        }, {
          value: '字符串'
        }, {
          value: '树'
        }, {
          value: '图论'
        }, {
          value: '动态规划'
        }, {
          value: '其它'
        }],
        value: '',
     }
   },

    methods:{
     submitSkill:function(){
       if(this.textarea1 == '' || this.textarea2 == '' || this.value == ''){
          this.$message({
            message: '不能为空！！',
            type: 'warning'
          })
          return
       }

      var reg = new RegExp("\'","g");
      this.textarea2 = this.textarea2.replace(reg, "\'\'");

       fetch(this.$store.state.fetchUrl + '/submitSkill', {
         method: 'POST',
         mode:'cors',
         body:JSON.stringify({
           'title': this.textarea1,
           'code':this.textarea2,
           'category':this.value
         })
       }).then(res => res.text())
       .then(data => {
         if(data == 'success'){
            this.$message({
              message: '提交成功 ~~',
              type: 'success'
          })
          this.textarea1 = ''
          this.textarea2 = ''
         }
         else{
            this.$message.error('出错了...')
         }
       })
     }
   }

}
</script>