<template>
<div v-loading="creating">
    <span v-for="i in 10" :key="i">&nbsp;</span>
    <el-button @click="drawer = true" type="primary" style="margin-left: 16px;" icon="el-icon-edit-outline" round>
    我要留言
    </el-button>

    <el-drawer
    :visible.sync="drawer"
    direction="btt"
    size='300px'>
        <el-input
            style="margin-left:8%; width:80%;"
            type="textarea"
            :autosize="{ minRows: 8, maxRows: 8}"
            placeholder="随便说说 ~~"
            v-model="textarea">
        </el-input><br>
        <div style="float:right; margin-right: 12%; margin-top:20px;">
        <FaceBox :textarea="textarea" @FromFaceBox="insertFace"></FaceBox>
        <ColorBox :textarea="textarea" @FromColorBox="colorWord"></ColorBox>
        <el-button type="primary" plain @click="submitMessage">OK !</el-button>
        </div>
    </el-drawer>

    <ul style="list-style-type:none; padding:10px;">
      <li v-for="(item, index) in realContent" :key="index">
            <el-container>
              <div>
                <el-avatar :size="50" :src="faceUrl(avatarID[index])" style="margin-top:25px; border: 2px solid rgb(78, 129, 224);"></el-avatar>
              </div>
              <el-aside width="90%">
                <el-card class="card3" :style="cardStyleList[index%7]">
                <div slot="header" class="clearfix">
                  <div style="float:left; "><strong>{{username[index]}}</strong></div>
                  <div style="float:right; ">
                    <span>{{date[index]}}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                    <el-button v-if="$store.getters.currentUser == username[index]" class="delete-button" type="text" icon="el-icon-delete" @click="deleteMessage(index)"></el-button>
                  </div>
                </div>
                <div v-html="item" style="white-space:pre-wrap;"></div>
                </el-card>
              </el-aside>
            </el-container>
      </li>
    </ul>
    <el-pagination
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-size="pageSize"
      background
      layout="prev, pager, next"
      :total="totalNum">
    </el-pagination>
</div>
</template>

<script>
import FaceBox from "../components/FaceBox.vue"
import ColorBox from "../components/ColorBox.vue"

export default {
  name: "Message",
  components : {
    FaceBox,
    ColorBox
  },

    data () {
      return {
        drawer: false,
        textarea: '',
        creating: true,

        currentPage: 1,
        totalNum: 1,
        pageSize: 7,

        cardStyleList: [
            "border-color: #4CB8C4;",
            "border-color: #3C3B3F;",
            "border-color: #DD5E89;",
            "border-color: #EB3349;",
            "border-color: #11998e;",
            "border-color: #1488CC;",
            "border-color: #16A085;"
        ],
        content:[],
        realContent:[],
        date:[],
        username:[],
        avatarID:[],
      }
    },

    computed:{
        faceUrl(){
          return function(i){
            return require("../assets/avatar/"+ Number(i).toString() +".jpg")
          }
        },
    },

    methods: {
      colorWord:function(res){
          this.textarea = res
      },
      
      transferContent:function(){
          var colorList = ['#FF2717', '#0B9BE3', '#009606', '#F06291']
          var colorName = ['red', 'blue', 'green', 'pink']
          this.realContent.splice(0, this.realContent.length)

          for(var k = 0; k < this.content.length; k ++){
              console.log(this.content.length)
              var temp;
              var reg = new RegExp("\n","g");
              temp = this.content[k].replace(reg, "<br>");
              for(var i = 0; i <= 106; i ++){
                reg = new RegExp("@pokemon\\("+ i.toString() +"\\)","g");
                var imgsrc=require("../assets/face/图片" + i.toString() + ".jpg")
                var target = '<img src="'+ imgsrc +'" style="width:35px;height:35px;">'
                temp = temp.replace(reg, target)
              }
              for(var i = 0; i < colorList.length; i ++){
                var reg = new RegExp("@"+ colorName[i] +"\\([^)]+\\)","g");
                var fit = temp.match(reg)
                if(fit == null) continue
                for(var j = 0; j < fit.length; j ++){
                    var b = fit[j].replace(RegExp("@[^(]+\\("), '<span style="color:'+ colorList[i] +';">').replace(RegExp("\\)"), "</span>")
                    temp = temp.replace(fit[j], b)
                }
              }
              this.realContent.push(temp)
          }
      },

      handleCurrentChange: function(){
        this.creating = true
        fetch(this.$store.state.fetchUrl + "/appendMessage", {
          method: 'POST',
          mode: 'cors',
          body: JSON.stringify({
            'start': (this.currentPage - 1) * this.pageSize,
            'pageSize': this.pageSize
          })
        }).then(res => res.json())
        .then(data => {
          this.username.splice(0, this.pageSize)
          this.username.push.apply(this.username, data['nickname'])
          this.avatarID.splice(0, this.pageSize)
          this.avatarID.push.apply(this.avatarID, data['avatarID'])
          this.date.splice(0, this.pageSize)
          this.date.push.apply(this.date, data['time'])
          this.content = data['content']
          this.transferContent()
          setTimeout(() => {this.creating = false}, 200)
        })
      },

      submitMessage:function(){
       if(this.textarea == ''){
          this.$message({
            message: '不能为空！！',
            type: 'warning'
          })
          return
       }
       var reg = new RegExp("\'","g");
       this.textarea = this.textarea.replace(reg, "\'\'");
       fetch(this.$store.state.fetchUrl + '/submitMessage', {
         method: 'POST',
         mode:'cors',
         body:JSON.stringify({
           'content': this.textarea,
           'nickname':this.$store.getters.currentUser,
         })
       }).then(res => res.text())
       .then(data => {
         if(data == 'success'){
            this.$message({
              message: '提交成功 ~~',
              type: 'success'
          })
          this.textarea = ''
          this.drawer = false
          this.totalNum += 1
          this.currentPage = 1
          this.handleCurrentChange()
         }
         else{
            this.$message.error('出错了...')
         }
       })
       },

        deleteMessage:function(i){
          this.$confirm('此操作将永久删除该留言, 是否继续?', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {

        fetch(this.$store.state.fetchUrl + "/deleteMessage", {
            method: 'POST',
            mode: 'cors',
            body: JSON.stringify({
              'time': this.date[i],
              'username':this.username[i]
          })
        }).then(res => res.text())
        .then(data => {
            if(data == 'success'){
              this.$message({
                type: 'success',
                message: '删除成功!'
              })
              this.totalNum -= 1
              this.handleCurrentChange()
            }
            else this.$message.error('出错了...');
        })

        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
        },

        insertFace(res){
            this.textarea += res
        },
    },

    created(){
      fetch(this.$store.state.fetchUrl + "/loadMessage", {
        method: 'POST',
        mode: 'cors',
        body: JSON.stringify({
          'pageSize': this.pageSize
        })
      }).then(res => res.json())
      .then(data => {
        this.totalNum = data['totalNum']
        this.username = data['nickname']
        this.avatarID = data['avatarID']
        this.date = data['time']
        this.content = data['content']
        this.transferContent()
        setTimeout(() => {this.creating = false}, 200)
      })
    }
  }
</script>

<style>
.infinite-list-wrapper2 {
    color: #333;
    line-height: 20px;
    margin-right: 100px;
}

.card2{ 
    border-radius: .8125rem;
    font-size: 0.5625rem;
    font-weight: 400;
    color: rgb(94, 92, 92);
    margin: 20px;
}
.el-drawer__header{
    margin-bottom: 0px;
}

.clearfix{
  padding:0;
  height: 10px;
}

.el-textarea__inner{
  font-family:'微软雅黑';
}
</style>