<template>
    <div v-loading="creating">

        <el-select v-model="value" placeholder="请选择">
          <el-option-group
            v-for="group in options"
            :key="group.label"
            :label="group.label">
            <el-option
              v-for="item in group.options"
              :key="item.value"
              :value="item.value">
            </el-option>
          </el-option-group>
        </el-select>

        <el-input
            @keyup.enter.native="search"
            placeholder="请输入内容"
            prefix-icon="el-icon-search"
            clearable
            v-model="input">
        </el-input><span v-for="i in 10" :key="i">&nbsp;</span>
        <el-button type="primary" icon="el-icon-search" circle @click="search"></el-button>
        <el-divider></el-divider>

        <div v-if="this.isMethod" class="infinite-list-wrapper1"  v-loading="creating">
        <MethodList :title="title" :code="code" :note="note" :count="num" :maxCnt="num"></MethodList>
        </div>
        <div v-if="!this.isMethod">
        <NoteList :content="content" :realContent="realContent" :label="label" :time="time" :subject="subject" :colID="colID" @reload="research"></NoteList>
        </div>
        <div style="text-align:center; color:grey;">
        <p v-if="!num"><strong>没 有 哦 ...</strong></p>
        </div>

    </div>
</template>

<script>
import NoteList from "../components/NoteList.vue"
import MethodList from "../components/MethodList.vue"

export default {
  name: "Search",
  components : {
      NoteList,
      MethodList
  },

  data() {
    return {
      creating: false,
        cur: 0,

        options: [{
          label: '算法&&技巧',
            options: [{
              value: '所有'
            }, {
              value:  '搜索'
            }, {
              value:'排序'
            }, {
              value: '字符串'
            }, {
              value: '树'
            }, {
              value: '图论'
            }, {
              value: '动态规划'
            }, {
              value: '其它'
            }]
        }, {
          label: '笔记',
          options: [{
            value: '操作系统',
          }, {
            value: '计算机网络',
          }, {
            value: '概率论与数理统计',
          }, {
            value: '人工智能',
          }, {
            value: '自然语言处理',
          }]
        }],

        value: '所有',

      input: '',
      title:[],
      code:[],
      note:[],
      num: 0,

      content:[],
      realContent:[],
      subject:[],
      label:[],
      time:[],
      colID:[0,0,0,0,0,0,0],

      methods:['所有', '搜索','排序','字符串','树','图论','动态规划','其它'],
      isMethod:false,
    }
  },

  computed:{
      isRiver(){
          return  sessionStorage.getItem("userName") == 'River' || this.$store.getters.currentUser == 'River'
      },
  },

  methods:{
      transferContent:function(){
          var colorList = ['#FF2717', '#0B9BE3', '#009606', '#F06291']
          var colorName = ['red', 'blue', 'green', 'pink']
          this.realContent.splice(0, this.realContent.length)

          for(var k = 0; k < this.content.length; k ++){
              console.log(this.content.length)
              var temp;
              var reg = new RegExp("\n","g");
              temp = this.content[k].replace(reg, "<br>");
              for(var i = 0; i <= 106; i ++){
                reg = new RegExp("@pokemon\\("+ i.toString() +"\\)","g");
                var imgsrc=require("../assets/face/图片" + i.toString() + ".jpg")
                var target = '<img src="'+ imgsrc +'" style="width:35px;height:35px;">'
                temp = temp.replace(reg, target)
              }
              for(var i = 0; i < colorList.length; i ++){
                var reg = new RegExp("@"+ colorName[i] +"\\([^)]+\\)","g");
                var fit = temp.match(reg)
                if(fit == null) continue
                for(var j = 0; j < fit.length; j ++){
                    var b = fit[j].replace(RegExp("@[^(]+\\("), '<span style="color:'+ colorList[i] +';">').replace(RegExp("\\)"), "</span>")
                    temp = temp.replace(fit[j], b)
                }
              }
              this.realContent.push(temp)
          }
      },

      research:function(){
              fetch(this.$store.state.fetchUrl + '/searchNote',{
                  method:'POST',
                  mode:'cors',
                  body:JSON.stringify({
                      'subject':this.value,
                      'keyword':this.input
                  })
              }).then(res => res.json())
                  .then(data => {
                        if(this.num != 0){
                            this.content.splice(0, this.num)
                            this.label.splice(0, this.num)
                            this.subject.splice(0, this.num)
                            this.time.splice(0, this.num)
                        }
                        this.content.push.apply(this.content, data['content'])
                        this.transferContent()
                        this.subject.push.apply(this.subject, data['subject'])
                        this.label.push.apply(this.label, data['label'])
                        this.time.push.apply(this.time, data['time'])
                        this.num = data['nums']
                        setTimeout(() => {this.creating = false}, 200)
                  })
      },

      search: function(){
          if(this.value == ''){
            this.$message({
            message: '不能都为空！！',
            type: 'warning'
          })
            if(this.num != 0){
                this.code.splice(0, this.num)
                this.title.splice(0, this.num)
                this.note.splice(0, this.num)
                this.content.splice(0, this.num)
                this.subject.splice(0, this.num)
                this.label.splice(0, this.num)
                this.time.splice(0, this.num)
                this.num = 0
            }
          return
          }

          this.creating = true

          this.isMethod=false;
          for(var i in this.methods){
            if(this.methods[i] == this.value){
              this.isMethod=true;
              break;
            }
          }

          if(this.isMethod){
              fetch(this.$store.state.fetchUrl + '/searchMethod',{
                  method:'POST',
                  mode:'cors',
                  body:JSON.stringify({
                      'category':this.value,
                      'keyword':this.input
                  })
              }).then(res => res.json())
                  .then(data => {
                        if(this.num != 0){
                            this.code.splice(0, this.num)
                            this.title.splice(0, this.num)
                            this.content.splice(0, this.num)
                            this.note.splice(0, this.num)
                            this.label.splice(0, this.num)
                            this.subject.splice(0, this.num)
                            this.time.splice(0, this.num)
                        }
                        this.code.push.apply(this.code, data['code'])
                        this.title.push.apply(this.title, data['title'])
                        this.note.push.apply(this.note, data['note'])
                        this.num = data['nums']
                        setTimeout(() => {this.creating = false}, 200)
                  })
          }
          else{
              if(this.isRiver == false){
                  this.$message({
                    type: 'warning',
                    message: '笔记暂不开放~（笑）'
                  })
                  setTimeout(() => {this.creating = false}, 200)
                  return
              }

              fetch(this.$store.state.fetchUrl + '/searchNote',{
                  method:'POST',
                  mode:'cors',
                  body:JSON.stringify({
                      'subject':this.value,
                      'keyword':this.input
                  })
              }).then(res => res.json())
                  .then(data => {
                        if(this.num != 0){
                            this.code.splice(0, this.num)
                            this.title.splice(0, this.num)
                            this.note.splice(0, this.num)
                            this.content.splice(0, this.num)
                            this.label.splice(0, this.num)
                            this.subject.splice(0, this.num)
                            this.time.splice(0, this.num)
                        }
                        this.content.push.apply(this.content, data['content'])
                        this.transferContent()
                        this.subject.push.apply(this.subject, data['subject'])
                        this.label.push.apply(this.label, data['label'])
                        this.time.push.apply(this.time, data['time'])
                        this.num = data['nums']
                        setTimeout(() => {this.creating = false}, 200)
                  })
          }
      },

  }
}
</script>

<style>
.el-input__inner{
    border-color: rgb(101, 166, 226);
}
.el-input{
    width: 65%;
}

</style>