<template>
<div>
    <el-button-group>
      <el-button icon="el-icon-question" @click="howToPlay">玩法说明</el-button>
      <el-button icon="el-icon-circle-plus" @click="drawer1 = true">贡献单词</el-button>
      <el-button icon="el-icon-info" @click="drawer2 = true" :disabled="disable">查单词</el-button>
      <el-button icon="el-icon-s-data" @click="rank" :disabled="disable">排行榜</el-button>
    </el-button-group>

    <el-drawer
      :visible.sync="drawer1"
      size="400px"
      direction="rtl">
        <div>
        <el-input
          style="margin-left:10%;margin-top:20%;"
          placeholder="单词英文"
          v-model="newEnglish">
        </el-input>
        <el-input
          style="margin-left:10%;margin-top:30px;"
          placeholder="单词中文(空格隔开)"
          v-model="newChinese">
        </el-input>
        <el-input
          type="textarea"
          style="margin-left:10%; width:65%; margin-top:30px;"
          :autosize="{ minRows: 10, maxRows: 10}"
          placeholder="例句或其它提示"
          v-model="newSentence">
        </el-input><br>
        <el-button style="margin-left:10%; margin-top:30px;" type="primary" plain @click="submitNewWord">OK !</el-button>
        </div>
    </el-drawer>


    <el-drawer
      :visible.sync="drawer2"
      size='35%'
      direction="rtl">
        <el-input
          @keyup.enter.native="searchWord"
          style="margin-left:15%;margin-top:5%;"
          placeholder="单词英文或中文"
          v-model="keyword">
        </el-input><span v-for="i in 5" :key="i">&nbsp;</span>
        <el-button type="primary" icon="el-icon-search" circle @click="searchWord"></el-button>
        <div class="search-board" v-loading="creating">
        <ul v-for="(item, index) in ansWord" :key="index" style="list-style-type:none;">
            <el-card style="margin:30px; border-color: #3C3B3F;">
                <el-row><div style="font-size:20px;"><strong>{{item}}</strong></div></el-row>
                <el-divider></el-divider>
                <el-row>{{ansChinese[index]}}</el-row>
                <el-divider></el-divider>
                <el-row>e.g. {{ansSentence[index]}}</el-row>
            </el-card>
        </ul>
        </div>
    </el-drawer>


    <el-card style="margin-top:30px;margin-right:50%; margin-bottom:10px; border-color: #3C3B3F;">
      <div style="margin-left:5px;margin-bottom:30px;">
        <div style="float:left; "><strong>Exam</strong>&nbsp;&nbsp;  Lv.100</div>
      </div>
      <el-progress 
      :percentage="parseFloat(enemyHP)" 
      :color="customColorMethod"
      :stroke-width="30"
      :text-inside="true"></el-progress>
    </el-card>

    <el-image
      style="width: 100px; height: 100px"
      :src="enemyPic"
      fit="contain"></el-image>
    <el-image
      style="width: 100px; height: 100px; float:right;"
      :src="myPic"
      fit="contain"></el-image>

    <el-card style="margin-top:5px;margin-left:50%; border-color: #3C3B3F;">
      <div style="margin-left:5px;margin-bottom:30px;">
        <div style="float:left; "><strong>{{player}}</strong>&nbsp;&nbsp;  lv.100</div>
      </div>
      <el-progress 
      :percentage="parseFloat(myHP)" 
      :color="customColorMethod"
      :stroke-width="30"
      :text-inside="true"></el-progress>
    </el-card>
    
    <el-container style="margin-left:130px;">
    <el-card class="card3" :style="cardStyleList[state]">
        <el-row>
            <div style="float:left; font-size:40px;"><strong>{{word}}</strong></div>
        </el-row>
        <el-divider></el-divider>
        <el-row>e.g. {{sentence}}</el-row>
        <el-divider></el-divider>
        <el-row>
            <el-input @keyup.enter.native="attack" style="width:75%; margin-left:0; margin-right:10%;" v-model="input" placeholder="请输入单词中文"></el-input>
            <el-button plain @click="attack">attack!!</el-button>
        </el-row>
    </el-card>
    </el-container>

</div>
</template>

<script>
import { setTimeout } from 'timers';
  export default {
    data () {
      return {
        drawer1:false,
        drawer2:false,
        howtoplay:'这是一场破壳萌对战~~ 回答出正确的单词中文进行攻击(回车)！！【伤害=10*回答与答案的匹配度】【最终分数=我方剩余HP*10/对战回合数】 匹配度低于30%会受到对方的反击哦...回答出接近的答案来造成巨大的伤害吧~~',
        player: this.$store.getters.currentUser,
        cardStyleList: [
            "border-color: #3C3B3F; border-width: 2px; width:80%;", // normal
            "border-color: red; border-width: 2px; width:80%;", //wrong
            "border-color: rgb(0, 255, 34); border-width: 2px; width:80%;", // correct
        ],
        enemyPic:require("../assets/enemyPic.jpg"),
        myPic:require("../assets/myPic.jpg"),
        wordNum: 1,
        state: 0,
        word:'hello',
        wordID: 1,
        sentence:'Hello word!',
        input:'',
        enemyHP: 100,
        myHP:100,
        cnt:0,
        newEnglish:'',
        newChinese:'',
        newSentence:'',
        disable: false,
        keyword:'',
        ansWord:[],
        ansChinese:[],
        ansSentence:[],
        num:0,
        creating:false,
      }
    },


    methods:{
      rank:function(){
        this.$message({
          message:'施工中orz...',
          type:'warning'
        })
      },

      howToPlay:function(){
        const h = this.$createElement;

        this.$notify({
          title: '对战规则',
          message: this.howtoplay,
          duration:6000
        });
      },

      searchWord:function(){
              if(this.keyword == ''){
                this.$message({
                  message: '不能为空！！',
                  type: 'warning'
                })
                return
              }
              this.creating = true
              fetch(this.$store.state.fetchUrl + '/searchWord',{
                  method:'POST',
                  mode:'cors',
                  body:JSON.stringify({
                      'keyword':this.keyword,
                  })
              }).then(res => res.json())
                  .then(data => {
                        if(this.num != 0){
                            this.ansWord.splice(0, this.num)
                            this.ansChinese.splice(0, this.num)
                            this.ansSentence.splice(0, this.num)
                        }
                        this.ansWord.push.apply(this.ansWord, data['word'])
                        this.ansChinese.push.apply(this.ansChinese, data['Chinese'])
                        this.ansSentence.push.apply(this.ansSentence, data['sentence'])
                        this.num = data['nums']
                        setTimeout(() => {this.creating = false}, 200)
                  })
      },

      customColorMethod(percentage) {
        if (percentage <= 20) {
          return '#f56c6c';
        } else if (percentage <= 50) {
          return '#e6a23c';
        } else {
          return '#67c23a';
        }
      },

      attack:function(){
        this.disable = true
        this.cnt += 1
        fetch(this.$store.state.fetchUrl + "/wordattack", {
          method: 'POST',
          mode: 'cors',
          body: JSON.stringify({
            'totalNum': this.wordNum,
            'except': this.wordID,
            'word': this.word,
            'input': this.input
          })
        }).then(res => res.json())
        .then(data => {
            if(data['message'] == 'correct'){
                this.$message({
                    message: data['ans'],
                    type: 'success'
                })
                this.state = 2
                this.enemyHP -= data['HP']
                this.enemyHP = this.enemyHP.toFixed(2)
                if(this.enemyHP < 0) this.enemyHP = 0
            }
            else if(data['message'] == 'false'){
                this.$message({
                    message: data['ans'],
                    type: 'warning'
                })
                this.state = 1
                this.myHP -= data['HP']
                this.myHP = this.myHP.toFixed(2)
                if(this.myHP < 0) this.myHP = 0
            }
            else{
                this.$message.error('故障了...')
                return
            }
            if(this.enemyHP == 0){
                var score = Math.round(this.myHP*10.0/this.cnt)
                this.$alert('你的分数是： ' + score.toString(), '恭喜过关', {
                  confirmButtonText: '确定',
                    callback: action => {
                      this.enemyHP = 100
                      this.myHP = 100
                      this.state = 0
                      this.input = ''
                      this.wordID = 1
                      this.word = 'hello'
                      this.sentence = 'Hello word!'
                      this.cnt = 0
                      this.disable = false
                      this.$message({
                        type: 'info',
                        message: '再来一次~~'
                      });
                  }
                });
            }
            else if(this.myHP == 0){
                this.$alert('点击确认回复并再次挑战', '你的眼前一片黑暗...', {
                  confirmButtonText: '确定',
                    callback: action => {
                      this.enemyHP = 100
                      this.myHP = 100
                      this.state = 0
                      this.input = ''
                      this.wordID = 1
                      this.word = 'hello'
                      this.cnt = 0
                      this.sentence = 'Hello word!'
                      this.disable = false
                      this.$message({
                        type: 'info',
                        message: '重新开始~~'
                      });
                  }
                });
            }
            else{
              setTimeout(()=>{
                this.state = 0
                this.input = ''
                this.wordID = data['wordID']
                this.word = data['word']
                this.sentence = data['sentence']
              }, data['message'] == 'correct'? 500:1200)              
            }

        })

      },

     submitNewWord:function(){
       if(this.newEnglish == '' || this.newChinese == '' || this.newSentence == ' '){
          this.$message({
            message: '不能为空！！',
            type: 'warning'
          })
          return
       }
        var reg = new RegExp("\'","g");
        this.newSentence = this.newSentence.replace(reg, "\'\'");
       fetch(this.$store.state.fetchUrl + '/submitWord', {
         method: 'POST',
         mode:'cors',
         body:JSON.stringify({
             'English':this.newEnglish,
             'Chinese':this.newChinese,
             'sentence':this.newSentence
         })
       }).then(res => res.text())
       .then(data => {
         if(data == 'success'){
            this.$message({
              message: '提交成功 ~~',
              type: 'success'
          })
          this.newEnglish = ''
          this.newChinese = ''
          this.newSentence = ''
          this.wordNum += 1
         }
         else{
            this.$message.error('出错了...')
         }
       })
     }

    },
    created(){
        fetch(this.$store.state.fetchUrl + "/wordNum", {
          method: 'GET',
          mode: 'cors',
        }).then(res => res.json())
        .then(data => {
            this.wordNum = data['num']
        })
    }
  }
</script>

<style>
.search-board{
    overflow: auto; 
    height: 450px; 
    margin-top:10%;
    margin-right:10%;
}


/*定义滚动条样式（高宽及背景）*/ 
::-webkit-scrollbar { 
    width: 6px;   /* 滚动条宽度， width：对应竖滚动条的宽度  height：对应横滚动条的高度*/
    height:6px;
    background: white;
} 

/*定义滑块 样式*/ 
::-webkit-scrollbar-thumb { 
    border-radius: 3px; 
    background-color: #7bb6dd;
}

</style>