<template>
    <el-popover
        style=" padding:0 12px;"
        placement="top"
        trigger="hover">
        <ul style="list-style-type:none;overflow:auto;padding:0;">
        <li v-for="index in 4" :key="index" style="float:left;" @click="emitText(index - 1)">
            <el-button
                circle
                shadow="hover" 
                class="face-card" 
                :style="cardStyle(index - 1)">
            </el-button>
        </li>
        </ul>
        <el-button style="margin:0 10px;" slot="reference" type="primary" plain >Color !</el-button>
    </el-popover>
</template>

<script>
export default {
    props:['textarea'],

    data(){
        return{
            colorList:['#FF2717', '#0B9BE3', '#009606', '#F06291'],
            colorName:['red', 'blue', 'green', 'pink']
        }
    },

    computed:{
        cardStyle(){
            return function(i){
                return "border-width:2px; width:10px;height:20px;margin:5px; background:" + this.colorList[i] + ";"
            }
        }
    },

    methods:{
        emitText:function(i){
                var textareaItem = document.getElementsByTagName('textarea')
                var start = textareaItem["0"].selectionStart
                var end = textareaItem["0"].selectionEnd
                var text = textareaItem["0"].value
                var prefix = text.substring(0, start);
                var middle = '@'+ this.colorName[i] +'(' + text.substring(start, end) + ")";
                var suffix = text.substring(end, text.length);
                this.$emit('FromColorBox', prefix + middle + suffix);
        }
    },
}
</script>

<style>
.face-card:hover{
  border-color:#b3d8ff;
  border-width: 2px;
}
</style>