<template>
<div>
    <ul
      class="infinite-list"
      v-infinite-scroll="load"
      infinite-scroll-disabled="busy"
      style="list-style-type:none;">
      <li v-for="(item, index) in title" class="infinite-list-item" :key="index" @click="getDetail(index)">
            <el-card class="card1" shadow="hover" :style="cardStyleList[index%7]">
               {{item}}
            </el-card>
      </li>
    </ul>
    <p v-if="loading"><strong>加载中...</strong></p>
    <p v-if="noMore"><strong>没有更多了</strong></p>
</div>
</template>

<script>
export default {
    props: ['title', 'code', 'note', 'count', 'maxCnt'],

    data(){
        return{
            loading: false,
            noMore: false,
            cardStyleList: [
                "background: linear-gradient(90deg, #3C3B3F,#605C3C);",
                "background: linear-gradient(90deg, #4CB8C4,#3CD3AD);",
                "background: linear-gradient(90deg, #DD5E89,#F7BB97);",
                "background: linear-gradient(90deg, #EB3349,#F45C43);",
                "background: linear-gradient(90deg, #11998e,#38ef7d);",
                "background: linear-gradient(90deg, #1488CC,#2B32B2);",
                "background: linear-gradient(90deg, #16A085,#F4D03F);"
            ],
        }
    },

    computed: {
      busy () {
        return this.loading || this.noMore
      }
    },

    methods:{
      load () {
        this.loading = true
        setTimeout(() => {
          let clientHeight = document.documentElement.clientHeight || document.body.clientHeight;  // 设备/屏幕高度
          let scrollTop =window.pageYOffset ||document.documentElement.scrollTop ||document.body.scrollTop; // 到头部的距离
          let scrollHeight = document.body.scrollHeight; // 滚动条的总高度
          if(scrollTop + clientHeight >= scrollHeight){
                this.$emit('append')
          }
          this.loading = false
          if(this.count >= this.maxCnt) this.noMore = true
        }, 700)
      },

      getDetail: function(i){
          this.$router.push({
              path: '/detail',
              query:{
                  code: this.code[i],
                  title: this.title[i],
                  note: this.note[i]
              }
          })
      },
    },


}
</script>

<style>
.infinite-list-item .el-card__body {
    padding: 20px 0;
}

.card1{
    border-radius: .3125rem;
    font-size: 1.5625rem;
    font-weight: 500;
    color: #fff;
    margin: 3%;
}

.card1:hover{
    margin: 0;
}
</style>