<template>
  <div class="diary-page" v-loading="creating">
    <NoteList :content="content" :realContent="realContent" :label="label" :time="time" :subject="subject" :colID="colID" @reload="handleCurrentChange"></NoteList>
    <el-pagination
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-size="pageSize"
      background
      layout="prev, pager, next"
      :total="totalNum">
    </el-pagination>
  </div>
</template>

<script>
import NoteList from "../components/NoteList.vue"

  export default {
    name: "Note",
    components : {
      NoteList
    },

    data () {
      return {
        creating: true,
        cur: 0,
        currentPage: 1,
        totalNum: 1,
        pageSize: 3,

        content:[],
        realContent:[],
        label:[],
        time:[],
        subject:[],
        colID:[0,0,0,0,0,0,0]
      }
    },

    methods: {
      transferContent:function(){
          var colorList = ['#FF2717', '#0B9BE3', '#009606', '#F06291']
          var colorName = ['red', 'blue', 'green', 'pink']
          this.realContent.splice(0, this.realContent.length)

          for(var k = 0; k < this.content.length; k ++){
              console.log(this.content.length)
              var temp;
              var reg = new RegExp("\n","g");
              temp = this.content[k].replace(reg, "<br>");
              for(var i = 0; i <= 106; i ++){
                reg = new RegExp("@pokemon\\("+ i.toString() +"\\)","g");
                var imgsrc=require("../assets/face/图片" + i.toString() + ".jpg")
                var target = '<img src="'+ imgsrc +'" style="width:35px;height:35px;">'
                temp = temp.replace(reg, target)
              }
              for(var i = 0; i < colorList.length; i ++){
                var reg = new RegExp("@"+ colorName[i] +"\\([^)]+\\)","g");
                var fit = temp.match(reg)
                if(fit == null) continue
                for(var j = 0; j < fit.length; j ++){
                    var b = fit[j].replace(RegExp("@[^(]+\\("), '<span style="color:'+ colorList[i] +';">').replace(RegExp("\\)"), "</span>")
                    temp = temp.replace(fit[j], b)
                }
              }
              this.realContent.push(temp)
          }
      },


      handleCurrentChange: function(){
        this.creating = true
        fetch(this.$store.state.fetchUrl + "/appendNote", {
          method: 'POST',
          mode: 'cors',
          body: JSON.stringify({
            'start': (this.currentPage - 1) * this.pageSize,
            'pageSize': this.pageSize
          })
        }).then(res => res.json())
        .then(data => {
          this.label.splice(0, this.pageSize)
          this.label.push.apply(this.label, data['label'])
          this.content = data['content']
          this.transferContent();
          this.time.splice(0, this.pageSize)
          this.time.push.apply(this.time, data['time'])
          this.subject.splice(0, this.pageSize)
          this.subject.push.apply(this.subject, data['subject'])
          this.colID[0] = 0
          for(var i=1; i<this.label.length; i++){
              this.colID[i] = this.colID[i - 1] + this.label[i]==this.label[i - 1]?1:0
           }
          setTimeout(() => {this.creating = false}, 200)
        })
      },

    },

    created(){
      fetch(this.$store.state.fetchUrl + "/loadNote", {
        method: 'POST',
        mode: 'cors',
        body: JSON.stringify({
          'pageSize': this.pageSize
        })
      }).then(res => res.json())
      .then(data => {
        this.totalNum = data['totalNum']
        this.label = data['label']
       this.content = data['content']
       this.transferContent()
       this.subject = data['subject']
       this.time = data['time']
       this.colID[0] = 0
       for(var i=1; i<this.label.length; i++){
           this.colID[i] = this.colID[i - 1] + ((this.label[i]==this.label[i - 1])?0:1)
       }
        setTimeout(() => {this.creating = false}, 200)
      })
    }
  }
</script>

<style>
.diary-page {
    color: #333;
    line-height: 20px;
    margin-right: 100px;
}

.el-pagination{
    text-align: center;
}
</style>