<template>
  <div>
    <el-select v-model="value" placeholder="请选择类别">
        <el-option
            v-for="item in options"
            :key="item.value"
            :value="item.value">
        </el-option>
    </el-select>
    <el-input
    type="textarea"
    style="width:20%"
    :autosize="{ minRows: 1, maxRows: 2}"
    placeholder="算法名称 (标题)"
    v-model="textarea0">
    </el-input><span v-for="i in 5" :key="i">&nbsp;</span>
    <el-input
    type="textarea"
    style="width:40%; margin-left:5%;"
    :autosize="{ minRows: 1, maxRows: 2}"
    placeholder="简要说明 (副标题, 可空)"
    v-model="textarea1">
    </el-input>
    <div style="margin: 20px 0;"></div>
    <el-input
    type="textarea"
    :autosize="{ minRows: 5}"
    placeholder="详细说明"
    v-model="textarea3">
    </el-input>
    <div style="margin: 20px 0;"></div>
    <el-input
    type="textarea"
    :autosize="{ minRows: 20}"
    placeholder="代码模板"
    v-model="textarea2">
    </el-input><br><br>
    <el-button type="primary" plain @click="submitMethod">OK !</el-button>
  </div>
</template>

<script>
export default {
   data() {
     return {
       textarea0: '',
       textarea1: '',
       textarea2: '',
       textarea3: '',

       options: [{
          value:  '搜索'
        }, {
          value:'排序'
        }, {
          value: '字符串'
        }, {
          value: '树'
        }, {
          value: '图论'
        }, {
          value: '动态规划'
        }, {
          value: '其它'
        }],
        value: '',
     }
   },

   methods:{
     submitMethod:function(){
       if(this.textarea3 == '' || this.textarea2 == '' || this.textarea0 == '' || this.value == ''){
          this.$message({
            message: '不能为空！！',
            type: 'warning'
          })
          return
       }

      var reg = new RegExp("\'","g");
      this.textarea2 = this.textarea2.replace(reg, "\'\'");

       fetch(this.$store.state.fetchUrl + '/submitMethod', {
         method: 'POST',
         mode:'cors',
         body:JSON.stringify({
           'title': '【' + this.textarea0 + '】 ' + this.textarea1,
           'code':this.textarea2,
           'category':this.value,
           'note':this.textarea3
         })
       }).then(res => res.text())
       .then(data => {
         if(data == 'success'){
            this.$message({
              message: '提交成功 ~~',
              type: 'success'
          })
          this.textarea0 = ''
          this.textarea1 = ''
          this.textarea2 = ''
          this.textarea3 = ''
          this.value = ''
         }
         else{
            this.$message.error('出错了...')
         }
       })
     }
   }
}
</script>