<template>
    <div v-loading="creating" style="margin-top:45px;">
        <el-container class="card-container" v-show="!isLogin">

          <div style="margin:0 auto">
            <div>
            <transition name="el-fade-in-linear">
                <el-container>
                <el-card :style="picStyle" :body-style="{ padding: '0px' }">
                    <el-image
                    style="width: 400px; height: 400px"
                    :src="picSrc"
                    fit="fill"></el-image>
                </el-card>
                <el-card shadow="hover" :style="cardStyle">
                    <transition name="el-fade-in-linear">
                    <div v-show="isRegister" class="register-card" style="line-height:57px;margin:60px;">
                    <el-input
                    placeholder="取个昵称吧"
                    prefix-icon="el-icon-user"
                    v-model="newNickname"
                    clearable="">
                    </el-input>
                    <el-input
                    placeholder="密码"
                    v-model="newPassword1"
                    show-password>
                    </el-input>
                    <el-input
                    placeholder="再输一次密码"
                    v-model="newPassword2"
                    show-password
                    @keyup.enter.native="register">
                    </el-input><br>
                    <el-button id="register-button" type="warning" icon="el-icon-check" circle @click="register"></el-button>
                    <el-button type="text" @click="goLogin"> 去登陆！</el-button>
                    </div>
                    </transition>

                    <transition>
                    <div v-show="!isRegister" style="line-height:80px;margin:60px;">
                    <el-input
                    placeholder="昵称"
                    prefix-icon="el-icon-user"
                    v-model="nickname"
                    clearable
                    @keyup.enter.native="login">
                    </el-input>
                    <el-input
                    placeholder="密码"
                    v-model="password"
                    show-password
                    @keyup.enter.native="login">
                    </el-input><br>
                    <el-button style="margin-left:25%;" type="primary" icon="el-icon-check" circle @click="login"></el-button>
                    <el-button type="text" @click="goRegister"> 没注册？</el-button>
                    </div>
                    </transition>

                </el-card>
                </el-container>
            </transition>
            </div>
          </div>
          
        </el-container>

        <div v-show="isLogin">
        <div class="hello">
                <el-container>
                    <el-col class="button-container">
                        <el-button class="left-switch-button" type="primary" icon="el-icon-arrow-left" circle @click="turnLeft" :disabled="userform.avatarID==0"></el-button>
                    </el-col>
                    <div class="avatar-container">
                        <el-avatar id="avatar" :size="200" :src="avatar"></el-avatar>
                    </div>
                    <el-col  class="button-container">
                        <el-button class="right-switch-button" type="primary" icon="el-icon-arrow-right" circle @click="turnRight" :disabled="userform.avatarID==maxID"></el-button>
                    </el-col>
                </el-container>
            <div style="align:center;">
                <el-header style="margin-top:15px;">{{greeting}}&nbsp;&nbsp;&nbsp;{{userform.username}}</el-header>
                <el-button-group style="margin-top:15px;">
                  <el-button round type="primary" @click="submitAvatarID" :disabled="disabled">确认头像</el-button>
                  <el-button round type="primary" @click="logout">Logout</el-button>
                </el-button-group>
            </div>
        </div>
        </div>
    </div>
</template>

<script>
import { setTimeout } from 'timers';
export default {
    inject:['reload'],
    data(){
        return {
            creating:false,
            isRegister:false,
            disabled:true,
            nickname:'',
            password:'',
            newNickname:'',
            newPassword1:'',
            newPassword2:'',
            cardStyle:'',
            registerStyle:"text-align: center;line-height: 320px;border:solid #F45C43;border-width:3px;border-radius: 2.3125rem;font-size: 2.5625rem;color: #fff;margin: 10px -1px;background:white;width: 350px;height: 400px;",
            loginStyle:"text-align: center;line-height: 320px;border:solid #1488CC;border-width:3px;border-radius: 2.3125rem;font-size: 2.5625rem;color: #fff;margin: 10px -1px;background:white;width: 350px;height: 400px;",
            picStyle:'',
            picSrc:'',
            picSrc1:require('../assets/sheep.jpg'),
            picSrc2:require('../assets/tiger.jpg'),
            picStyle2:"text-align: center;line-height: 320px;border:solid #F45C43;border-width:3px;border-radius: 2.3125rem;font-size: 2.5625rem;color: #fff;margin: 10px -1px;width: 350px;height: 400px; background-position: center;",
            picStyle1:"text-align: center;line-height: 320px;border:solid #1488CC;border-width:3px;border-radius: 2.3125rem;font-size: 2.5625rem;color: #fff;margin: 10px -1px;width: 350px;height: 400px; background-position: center;",

            maxID: 66,
            userform:{
                username:'River',
                avatarID: 0,
            },
            
        }
    },

    computed:{
        isLogin(){
            if(sessionStorage.getItem("userName")){
                var user={
                    'username': sessionStorage.getItem("userName"),
                    'avatarID': sessionStorage.getItem("avatarID")
                }
                this.userform.username = sessionStorage.getItem("userName")
                this.userform.avatarID = sessionStorage.getItem("avatarID")
                this.$store.commit("userStatus", user)
            }
            else this.$store.commit("userStatus", null)
            return this.$store.getters.isLogin && sessionStorage.getItem("userName")!=String('null')
        },

        avatar(){
            return require("../assets/avatar/"+ this.userform.avatarID.toString() +".jpg")
        },

        greeting(){
            var now = new Date()
            var hour = now.getHours()
            if(hour < 10 && hour >= 1) return 'Good morining ~~'
            if(hour < 14) return 'Good day ~~'
            if(hour < 18) return 'Good afternoon ~~'
            if(hour < 21) return 'Good evening ~~'
            return 'Good night ~~'
        },

    },

    methods: {
        goRegister:function(){
            this.isRegister=true
            this.cardStyle=this.registerStyle
            this.picStyle=this.picStyle2
            this.picSrc = this.picSrc2
        },
        goLogin:function(){
            this.isRegister=false
            this.cardStyle=this.loginStyle
            this.picStyle=this.picStyle1
            this.picSrc = this.picSrc1
        },

        register:function(){
            if(this.newNickname == '' || this.newPassword1 == '' || this.newPassword2 == ''){
                this.$message({
                    message: '不能为空！！',
                    type: 'warning'
                })
                return
            }
            for(var i = 0; i < this.newNickname.length; i ++){
                var a = this.newNickname[i]
                if(a == ' ' || a == '/' || a == ':' || a == '&' || a == '*' || a == ';' || a == '"' || a == "'" || a== "%"){
                    this.$message({
                        message: '不能出现 [/：&*:"\'%] !! ',
                        type: 'warning'
                    })
                    return
                }
            }
            if(this.newPassword1 != this.newPassword2){
                this.$message({
                    message: '两次密码不一致！！',
                    type: 'warning'
                })
                return
            }
            fetch(this.$store.state.fetchUrl + '/register', {
                method: 'POST',
                mode:'cors',
                body:JSON.stringify({
                    'nickname': this.newNickname,
                    'password': this.newPassword1
                })
            }).then(res => res.text())
            .then(data => {
                if(data == 'already-exist'){
                    this.$message({
                        message: '该昵称已被注册 ...',
                        type: 'warning'
                    })
                }
                else if(data == 'success'){
                    this.$message({
                        message: '注册成功！！',
                        type: 'success'
                    })
                    setTimeout(()=>{this.goLogin()}, 1000)
                }
                else{
                    this.$message.error('出错了...')
                }
            })
        },

        login:function(){
            if(this.nickname == '' || this.password == ''){
                this.$message({
                    message: '不能为空！！',
                    type: 'warning'
                })
                return
            }
            this.creating = true

            fetch(this.$store.state.fetchUrl + '/login', {
                method: 'POST',
                mode:'cors',
                body:JSON.stringify({
                    'nickname': this.nickname,
                    'password': this.password
                })
            }).then(res => res.json())
            .then(data => {
                if(data['data'] == 'noNickname'){
                    this.$message({
                        message: '该昵称不存在 ...',
                        type: 'warning'
                    })
                }
                else if(data['data'] == 'wrong'){
                    this.$message({
                        message: '密码错误！！',
                        type: 'warning'
                    })
                }
                else if(data['data'] == 'success'){
                    this.$message({
                        message: '登录成功！！',
                        type: 'success'
                    })
                    this.userform.avatarID = data['avatarID']
                    sessionStorage.setItem("userName", this.nickname)
                    sessionStorage.setItem("avatarID", this.userform.avatarID)
                    var user={
                        'username': this.nickname,
                        'avatarID': this.userform.avatarID
                    }
                    this.$store.dispatch("setUser", user)
                    this.userform.username = this.nickname
                    this.reload()
                }
                else{
                    this.$message.error('出错了...')
                }
            })
            setTimeout(()=>{this.creating=false}, 500)
        },

        logout:function(){
            this.$confirm('确认退出?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                    type: 'warning'
            }).then(() => {
                this.$store.dispatch("setUser", null)
                sessionStorage.clear();
                this.$message({
                    type: 'success',
                    message: '退出成功！!'
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消'
                });          
            });
        },

        turnLeft:function(){
            this.disabled = false
            this.userform.avatarID -= 1
            document.getElementById('avatar').setAttribute('style', 'width:200px; height:200px;border: 4px dashed rgb(78, 129, 224); margin-bottom:0px;')
        },

        turnRight:function(){
            this.disabled = false
            this.userform.avatarID = Number(this.userform.avatarID) + 1
            document.getElementById('avatar').setAttribute('style', 'width:200px; height:200px;border: 4px dashed rgb(78, 129, 224); margin-bottom:0px;')
        },

        submitAvatarID:function(){
            fetch(this.$store.state.fetchUrl + "/submitAvatarID", {
                method:'POST',
                mode:'cors',
                body:JSON.stringify({
                    'avatarID':this.userform.avatarID,
                    'nickname':this.$store.getters.currentUser
                })
            }).then(res => res.text())
            .then(data => {
                if(data == "success"){
                    this.$message({
                        type: 'success',
                        message: '头像更改成功 ~~'
                    }); 
                    this.disabled = true
                    document.getElementById('avatar').setAttribute('style', 'width:200px; height:200px;border: 4px solid rgb(78, 129, 224); margin-bottom:0px;')
                    sessionStorage.setItem("avatarID", this.userform.avatarID)
                    var user={
                        'username': this.nickname,
                        'avatarID': this.userform.avatarID
                    }
                    this.$store.dispatch("setUser", user)
                }
                else{
                    this.$message.error('出错了...')
                }
            })
        }
    },

    created(){
        this.cardStyle = this.loginStyle
        this.picStyle = this.picStyle1
        this.picSrc = this.picSrc1
    }
}
</script>

<style>
.card-container{
    height: 460px;
    overflow: hidden;
    font-weight: 300;
}

.register-card .el-input__inner {
    border-color: #F45C43;
}

.el-card .el-input{
    width:100%;
}

#register-button{
    margin-left:25%;
    border:1px solid #F45C43;
    background-color: #F45C43;
}

#register-button:hover{
    border:1px solid rgb(243, 126, 108);
    background-color: rgb(243, 126, 108);
}

.hello{
    overflow: hidden;
    font-weight: 300;
    font-size: 2.5rem;
    text-align:center;
    font-family:'Mirages Custom', 'Merriweather', 'Open Sans', 'PingFang SC', 'Hiragino Sans GB', 'Microsoft Yahei', 'WenQuanYi Micro Hei',  'Segoe UI Emoji', 'Segoe UI Symbol', Helvetica, Arial, sans-serif;
}

.button-container{
    height:100%;
}

.avatar-container{
    width:40%;
    height:100%;
    margin:0 auto;
}

#avatar{
    width:200px; 
    height:200px;
    border: 4px solid rgb(78, 129, 224);
    margin-bottom:0px;
}
#avatar:hover{
    -webkit-transform:rotate(45deg);
    transform:rotate(45deg);
    -webkit-transition:-webkit-transform .5s linear;
    transition:transform .5s linear;
}

.left-switch-button{
    margin-left: 200px;
    margin-top: 80px;
}

.right-switch-button{
    margin-right:200px;
    margin-top: 80px;
}
</style>